button.navbar-toggler {
	display: none;
	position: relative;
	top: 0px;
	left: 0px;
	padding: 13px;
	z-index: 1;
	border: 0;
	-webkit-user-select: none;
	user-select: none;
	background-color: #054435;
}

@media (max-width: 767px) {
	button.navbar-toggler {
		display: block;
	}
}

button.navbar-toggler input {
	display: block;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	margin-top: 0;
	cursor: pointer;
	opacity: 0;
	/* hide this */
	z-index: 2;
	/* and place it over the hamburger */
	-webkit-touch-callout: none;
}

/*
 * Just a quick hamburger
 */
button.navbar-toggler span {
	display: block;
	width: 33px;
	height: 4px;
	margin-bottom: 5px;
	position: relative;
	background: #cdcdcd;
	border-radius: 3px;
	z-index: 1;
	transform-origin: 20px 0px;
	transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}

button.navbar-toggler span:first-child {
	transform-origin: 0% 0%;
}

button.navbar-toggler span:nth-last-child(2) {
	transform-origin: 65% 100%;
}

/* 
 * Transform all the slices of hamburger
 * into a crossmark.
 */
button.navbar-toggler input:checked ~ span {
	opacity: 1;
	transform: rotate(45deg) translate(-2px, -1px);
	background: #232323;
}

/*
 * But let's hide the middle one.
 */
button.navbar-toggler input:checked ~ span:nth-last-child(3) {
	opacity: 0;
	transform: rotate(0deg) scale(0.2, 0.2);
}

/*
 * Ohyeah and the last one should go the other direction
 */
button.navbar-toggler input:checked ~ span:nth-last-child(2) {
	transform: rotate(-45deg) translate(0, -1px);
}

/*
 * Make this absolute positioned
 * at the top left of the screen
 */
#menu {
	position: absolute;
	width: 300px;
	margin: -100px 0 0 -50px;
	padding: 50px;
	padding-top: 125px;
	background: #ededed;
	list-style-type: none;
	-webkit-font-smoothing: antialiased;
	/* to stop flickering of text in safari */
	transform-origin: 0% 0%;
	transform: translate(-100%, 0);
	transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

/*
 * And let's slide it in from the left
 */
button.navbar-toggler input:checked ~ ul {
	transform: none;
}

span.line:last-of-type {
	margin-bottom: 0;
}

