.blog-landing article a.btn {
	width: 100%;
}

.blog-landing article h2.entry-title {
	font-size: 24px;
}

.blog-landing article .entry-meta,
.blog-landing article .entry-footer {
	display: none;
}

main.article-single {
	margin: 5rem 0;
}

main.article-single .entry-meta {
	margin-bottom: 25px;
}

main.article-single a.read-more.btn,
main.article-single .entry-footer,
main.article-single .comment-notes,
main.article-single .comment-form-comment,
main.article-single .comment-form-author,
main.article-single .comment-form-email,
main.article-single .comment-form-url,
main.article-single .comment-form-cookies-consent,
main.article-single .form-submit,
main.article-single #reply-title {
